/* Fondo y fuente general */
body {
margin: 0;
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
background-color: #141414;
color: #ffffff;
}
/* Contenedor principal */
.App {
max-width: 1200px;
margin: auto;
padding: 2rem;
}

/* Título principal */
.titulo {
font-size: 88px;
font-weight: bold;
color: #e50914; /* rojo Netflix */
text-align: center;
letter-spacing: 5px;
font-family: 'Bebas Neue', Gadget, sans-serif;
margin-bottom: 20px;
}
/* Buscador */
.buscador {
display: flex;
flex-wrap: wrap;
gap: 10px;
justify-content: center;
margin-bottom: 2rem;
}

.buscador input {
padding: 12px;
font-size: 1rem;
border-radius: 8px;
border: none;
width: 300px;
}

.buscador button {
background-color: #e50914;
color: white;
padding: 12px 18px;
border: none;
border-radius: 8px;
cursor: pointer;
font-weight: bold;
transition: background 0.3s ease;
}
.buscador button:hover {
background-color: #b00610;
}
.btn-ia {
background-color: #ffa500;
}
.btn-ia:hover {
background-color: #cc8400;
}
/* Bloque de recomendación IA */
.bloque-recomendaciones {
background-color: #1e1e1e;
padding: 1.5rem;
margin-bottom: 2rem;
border-radius: 10px;
box-shadow: 0 0 10px rgba(229, 9, 20, 0.4);
}
/* Galería de películas */
.galeria {
margin-top: 2rem;
}
.galeria h2 {
margin-bottom: 1rem;
font-size: 1.5rem;
color: #ffffff;
}

/* Grid de tarjetas */
.grid {
display: grid;
gap: 1.5rem;
grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
justify-items: center;
}
/* Tarjeta de película */
.tarjeta {
background-color: #1e1e1e;
border-radius: 12px;
overflow: hidden;
transition: transform 0.3s ease;
cursor: pointer;
width: 140px;
display: flex;
flex-direction: column;
align-items: center;
}

.tarjeta:hover {
transform: scale(1.08);
}
.tarjeta img {
width: 100%;
height: 210px;
object-fit: cover;
border-bottom: 2px solid #e50914;
transition: opacity 0.3s;
}
.info {
padding: 10px;
text-align: center;
}

.info h3 {
margin: 0.2rem 0;
font-size: 1rem;
color: #ffffff;
}

.info p {
font-size: 0.8rem;
color: #aaaaaa;
margin-bottom: 0.3rem;
}
.info span {
font-size: 0.75rem;
color: #e50914;
}